    '': 'O',
    '': 'U',
    '': 'U',
    '': 'U',
    '': 'U',
    '': 'Y',
    '': 'o',
    '': 'B',
    '': 'a',
    '': 'a',
    '': 'a',
    '': 'a',
    '': 'a',
    '': 'a',
    '': 'a',
    '': 'c',
    '': 'e',
    '': 'e',
    '': 'e',
    '': 'e',
    '': 'i',
    '': 'i',
    '': 'i',
    '': 'i',
    '': 'd',
    '': 'n',
    '': 'o',
    '': 'o',
