    '': 'o',
    '': 'o',
    '': 'o',
    '': 'u',
    '': 'u',
    '': 'u',
    '': 'u',
    '': 'y',
    '': 'o',
    '': 'y',

    // Cyrillic
    '': 'A',
    '': 'B',
    '': 'B',
    '': 'F',
    '': 'A',
    '': 'E',
    '': 'K',
    '': '3',
    '': 'N',
    '': 'N',
    '': 'K',
    '': 'N',
    '': 'M',
    '': 'H',
    '': 'O',
    '': 'N',
    '': 'P',
    '': 'C',
    '': 'T',
    '': 'y',
